<template>
  <article>
    <h1><code>&lt;veui-alert&gt;</code></h1>
    <section>
      <div></div>
      <veui-alert type="success" message="" @update:open="close"></veui-alert>
      <veui-alert class="limit-width" type="success" message="" @update:open="close"></veui-alert>
      <veui-alert type="warning" message="" @update:open="close"></veui-alert>
      <veui-alert class="limit-width" type="warning" :message="messages" @update:open="close"></veui-alert>
      <veui-alert type="info" message="" @update:open="close"></veui-alert>
      <veui-alert class="limit-width" type="info" message="" @update:open="close" close-text=""></veui-alert>
      <veui-alert type="error" message="" @update:open="close"></veui-alert>
      <veui-alert class="limit-width" type="error" message="" @update:open="close"></veui-alert>
      <veui-dialog
          title=""
          :open="open"
          @update:open="open = !open"
          :draggable="true">
          
          <template slot="foot"></template>
      </veui-dialog>
    </section>
  </article>
</template>

<script>
import bus from '../bus'
import { Alert, Dialog, Button, Icon } from 'veui'

export default {
  name: 'alert',
  components: {
    'veui-alert': Alert,
    'veui-dialog': Dialog,
    'veui-button': Button,
    'veui-icon': Icon
  },
  data () {
    return {
      open: false,
      messages: [
        '1111',
        '2',
        '3',
        '4',
        '5'
      ],
      messageIndex: 0
    }
  },
  methods: {
    close () {
      this.open = true
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })
  }
}
</script>

<style scoped>
section {
  margin: 30px;
}

.veui-alert {
  margin: 30px 0;
}

.veui-alert.limit-width {
  width: 340px;
}
</style>



