<template>
  <article>
    <h1><code>&lt;veui-button&gt;</code></h1>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="primary"</span>
      </p>
      <p>
        <veui-button ui="primary small"></veui-button>
        <span class="veui-font-level-2d">ui=small</span>
      </p>
      <p>
        <veui-button ui="primary"></veui-button>
        <span class="veui-font-level-2d">ui</span>
      </p>
      <p>
        <veui-button ui="primary large"></veui-button>
        <span class="veui-font-level-2d">ui=large</span>
      </p>
      <p>
        <veui-button ui="primary" disabled></veui-button>
        <span class="veui-font-level-2d">disabled</span>
      </p>
      <p>
        <veui-button ui="primary" loading></veui-button>
        <span class="veui-font-level-2d">loading</span>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui</span>
      </p>
      <p>
        <veui-button ui="small"></veui-button>
        <veui-button></veui-button>
        <veui-button ui="large"></veui-button>
        <veui-button disabled></veui-button>
        <veui-button loading></veui-button>
      </p>
    </section>
    <p>
      <veui-button ui="small"><icon name="cross-small"></icon> </veui-button>
      <veui-button><icon name="cross-small"></icon> </veui-button>
      <veui-button ui="large"><icon name="cross-small"></icon> </veui-button>
      <veui-button disabled><icon name="cross-small"></icon> </veui-button>
      <veui-button loading><icon name="cross-small"></icon> </veui-button>
    </p>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="alt"</span>
      </p>
      <p>
        <veui-button ui="alt small"></veui-button>
        <veui-button ui="alt"></veui-button>
        <veui-button ui="alt large"></veui-button>
        <veui-button ui="alt" disabled></veui-button>
        <veui-button ui="alt" loading></veui-button>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="link"</span>
      </p>
      <p>
        <veui-button ui="link primary small"></veui-button>
        <veui-button ui="link primary"></veui-button>
        <veui-button ui="link primary large"></veui-button>
        <veui-button ui="link primary" disabled></veui-button>
        <veui-button ui="link primary" loading></veui-button>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="round"</span>
      </p>
      <p>
        <veui-button ui="round primary small"><icon name="check"></icon></veui-button>
        <veui-button ui="round primary"><icon name="check"></icon></veui-button>
        <veui-button ui="round primary large"><icon name="check"></icon></veui-button>
        <veui-button ui="round primary" disabled><icon name="check"></icon></veui-button>
        <veui-button ui="round primary" loading><icon name="check"></icon></veui-button>
      </p>
    </section>
    <p>
      <veui-button ui="round small"><icon name="edit"></icon></veui-button>
      <veui-button ui="round"><icon name="edit"></icon></veui-button>
      <veui-button ui="round large"><icon name="edit"></icon></veui-button>
      <veui-button ui="round" disabled><icon name="edit"></icon></veui-button>
      <veui-button ui="round" loading><icon name="edit"></icon></veui-button>
    </p>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="square"</span>
      </p>
      <p>
        <veui-button ui="square small"><icon name="search"></icon></veui-button>
        <veui-button ui="square"><icon name="search"></icon></veui-button>
        <veui-button ui="square large"><icon name="search"></icon></veui-button>
        <veui-button ui="square" disabled><icon name="search"></icon></veui-button>
        <veui-button ui="square" loading><icon name="search"></icon></veui-button>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="alt square"</span>
      </p>
      <p>
        <veui-button ui="alt square small"><icon name="cross"></icon></veui-button>
        <veui-button ui="alt square"><icon name="cross"></icon></veui-button>
        <veui-button ui="alt square large"><icon name="cross"></icon></veui-button>
        <veui-button ui="alt square" disabled><icon name="cross"></icon></veui-button>
        <veui-button ui="alt square" loading><icon name="cross"></icon></veui-button>
      </p>
    </section>
    <section>
      <h2></h2>
      <p v-text="sample" class="sample"></p>
    </section>
    <section class="attrTab">
      <h2>Attributes</h2>
      <veui-table :data="attrData">
        <veui-table-column field="parameter" title="" width="10%"></veui-table-column>
        <veui-table-column field="desc" title="" width="40%"></veui-table-column>
        <veui-table-column field="type" title="" width="10%"></veui-table-column>
        <veui-table-column field="canSelectVal" title="" width="30%"></veui-table-column>
        <veui-table-column field="defaultVal" title="" width="10%"></veui-table-column>
      </veui-table>
    </section>
  </article>
</template>

<script>
import bus from '../bus'
import { Button, Icon, Table, Column } from 'veui'
import 'veui-theme-blue/icons/cross-small'
import 'veui-theme-blue/icons/check'
import 'veui-theme-blue/icons/edit'
import 'veui-theme-blue/icons/search'
import 'veui-theme-blue/icons/cross'

export default {
  name: 'button-demo',
  components: {
    Icon,
    'veui-button': Button,
    'veui-table': Table,
    'veui-table-column': Column
  },
  data() {
    return {
      sample: '<veui-button ui="large" disabled></veui-button>',
      attrData: [
        {
          parameter: 'ui', desc: '', type: 'string', canSelectVal: 'uiprimarylinkroundsquarealtuismalllarge', defaultVal: '--'
        },
        {
          parameter: 'disabled', desc: '', type: 'boolean', canSelectVal: '--', defaultVal: 'false'
        },
        {
          parameter: 'loading', desc: '', type: 'boolean', canSelectVal: '--', defaultVal: 'false'
        }
      ]
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })
  }
}
</script>

<style scoped>
p {
  user-select: none;
}

.veui-button {
  margin-right: 10px;
}
section {
  margin-bottom: 20px;
}
</style>



